<!-- src\views\BlogsView.vue: -->
<template>
  <div class="blog-container">
    <!-- Particles background -->
    <vue-particles
      id="tsparticles"
      @particles-loaded="particlesLoaded"
      :options="particlesOptions"
    />

    <!-- Blog content -->
    <div class="container blog-content mt-2" :class="animationClass">
      <div class="row d-flex justify-content-around">
        <div class="col-md-12 text-right text-white">
          <h1 class="text-warning">BLOGS</h1>
          <h3 class="text-white">TECH INSIGHTS</h3>
        </div>
      </div>

      <!-- Blog Cards Carousel -->
      <Carousel
        v-bind="settings"
        :breakpoints="breakpoints"
        :autoplay="3500"
        :wrap-around="true"
        :pauseAutoplayOnHover="true"
        :transition="500"
      >
        <Slide v-for="blog in blogs" :key="blog.id">
          <div class="blog-card">
            <div class="blog-image-container">
              <img class="blog-image" :src="blog.image" :alt="blog.name" />
              <div class="blog-overlay">
                <div class="blog-actions">
                  <button
                    @click="openBlogDetails(blog)"
                    class="action-btn read-btn"
                  >
                    <i class="fas fa-book-open"></i> Read More
                  </button>
                </div>
              </div>
            </div>
            <div class="blog-info">
              <div class="blog-meta">
                <span class="blog-category text-warning">{{ blog.name }}</span>
              </div>
              <p class="blog-excerpt">
                {{ truncate(blog.description, 100) }}
              </p>

              <div class="blog-actions mt-auto mb-2">
                <button
                  @click.stop="openBlogDetails(blog)"
                  class="action-btn details-btn"
                >
                  <i class="fas fa-info-circle"></i> Read Article
                </button>
              </div>
            </div>
          </div>
        </Slide>

        <template #addons>
          <Navigation />
          <Pagination />
        </template>
      </Carousel>
    </div>

    <!-- Blog Details Modal -->
    <div class="modal-overlay" v-if="selectedBlog" @click="closeBlogDetails">
      <div class="modal-content" @click.stop>
        <button class="modal-close" @click="closeBlogDetails">Ã—</button>

        <div class="modal-header">
          <h2>{{ selectedBlog.name }}</h2>
          <div class="modal-meta"></div>
        </div>

        <div class="modal-body">
          <img
            class="modal-image"
            :src="selectedBlog.image"
            :alt="selectedBlog.name"
          />

          <div class="modal-details">
            <div class="modal-text">
              <p v-html="selectedBlog.fullDescription"></p>
            </div>

            <div class="modal-tags">
              <h4>Topics</h4>
              <div class="tags-stack">
                <span
                  class="tag-badge"
                  v-for="(tag, idx) in selectedBlog.tags"
                  :key="idx"
                >
                  {{ tag }}
                </span>
              </div>
            </div>
          </div>
        </div>

        <div class="modal-footer">
          <a href="#" class="modal-btn">
            <i class="fas fa-share-alt"></i> Share Article
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import blogData from "@/assets/data/blogs.json";
import { defineComponent } from "vue";
import { Carousel, Navigation, Pagination, Slide } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";

export default defineComponent({
  name: "BlogSection",
  components: {
    Carousel,
    Slide,
    Pagination,
    Navigation,
  },
  data() {
    return {
      animationClass: "fade-in",
      selectedBlog: null,
      // Carousel settings
      settings: {
        itemsToShow: 1,
        snapAlign: "center",
      },
      // Responsive breakpoints
      breakpoints: {
        400: {
          itemsToShow: 1.5,
          snapAlign: "center",
        },
        700: {
          itemsToShow: 2.5,
          snapAlign: "center",
        },
        1200: {
          itemsToShow: 3.5,
          snapAlign: "center",
        },
      },
      // Particles configuration
      particlesOptions: {
        background: {
          color: {
            value: "transparent",
          },
        },
        fullScreen: {
          enable: true,
          zIndex: -1,
        },
        responsive: [
          {
            maxWidth: 768,
            options: {
              particles: {
                number: {
                  value: 15, // Reduce particles on mobile
                },
              },
            },
          },
        ],
        fpsLimit: 60,
        interactivity: {
          detectsOn: "window",
          events: {
            onClick: {
              enable: true,
              mode: "push",
            },
            onHover: {
              enable: true,
              mode: "grab",
              parallax: {
                enable: true,
                force: 60,
                smooth: 100,
              },
            },
            touchstart: {
              // Add this to handle touch events properly
              enable: true,
              mode: "push",
            },
            touchmove: {
              // Add this to allow scrolling while touching particles
              enable: true,
              mode: "grab",
            },
          },
          modes: {
            grab: {
              distance: 100,
              links: {
                opacity: 0.8,
                color: "#ffffff",
              },
            },
            push: {
              quantity: 4,
            },
          },
        },
        particles: {
          color: {
            value: "#64ffda",
          },
          links: {
            color: "#64ffda",
            distance: 100,
            enable: true,
            opacity: 0.4,
            width: 1,
          },
          move: {
            direction: "none",
            enable: true,
            outModes: "bounce",
            random: false,
            speed: 2,
            straight: false,
          },
          number: {
            density: {
              enable: true,
              area: 800,
            },
            value: 100,
          },
          opacity: {
            value: 0.5,
            animation: {
              enable: true,
              speed: 1,
              minimumValue: 0.1,
            },
          },
          shape: {
            type: "circle",
          },
          size: {
            value: { min: 1, max: 3 },
            animation: {
              enable: true,
              speed: 2,
              minimumValue: 0.1,
            },
          },
        },
        detectRetina: true,
      },
      blogs: blogData.blogs,
    };
  },
  methods: {
    toggleAnimation() {
      this.animationClass =
        this.animationClass === "fade-in" ? "bounce-in" : "fade-in";
    },
    truncate(text, length) {
      if (text.length <= length) return text;
      return text.substring(0, length) + "...";
    },
    openBlogDetails(blog) {
      this.selectedBlog = blog;
      document.body.style.overflow = "hidden"; // Prevent scrolling behind modal
    },
    closeBlogDetails() {
      this.selectedBlog = null;
      document.body.style.overflow = ""; // Restore scrolling
    },
  },
});
</script>

<style scoped>
/* Import Font Awesome for icons */
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css");

.blog-container {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.blog-content {
  position: relative;
  z-index: 1;
}

/* Blog Card Styling - FIXED DESIGN */
.blog-card {
  background: rgba(17, 34, 64, 0.7);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  height: 400px; /* Fixed height for consistency */
  display: flex;
  flex-direction: column;
  margin: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(100, 255, 218, 0.1);
  backdrop-filter: blur(5px);
}

.blog-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(100, 255, 218, 0.2);
  border-color: rgba(100, 255, 218, 0.3);
}

/* Image container - FIXED SIZE */
.blog-image-container {
  position: relative;
  overflow: hidden;
  height: 180px; /* Fixed height */
  flex-shrink: 0; /* Prevent shrinking */
  background: linear-gradient(135deg, #0a192f 0%, #172a45 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.blog-image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Changed to cover for consistent filling */
  object-position: center;
  transition: transform 0.5s ease;
}

.blog-card:hover .blog-image {
  transform: scale(1.1);
}

/* Enhanced overlay with button */
.blog-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 25, 47, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.blog-card:hover .blog-overlay {
  opacity: 1;
}

.blog-actions {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.action-btn {
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 600;
  font-size: 0.85rem;
  transition: all 0.3s ease;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  border: none;
  white-space: nowrap;
}

.details-btn {
  background: #64ffda;
  color: #0a192f;
}

.details-btn:hover {
  background: transparent;
  color: #64ffda;
  box-shadow: inset 0 0 0 2px #64ffda;
}

.read-btn {
  background: transparent;
  color: #64ffda;
  box-shadow: inset 0 0 0 2px #64ffda;
}

.read-btn:hover {
  background: #64ffda;
  color: #0a192f;
}

/* Blog Info - FIXED LAYOUT */
.blog-info {
  padding: 1.5rem;
  flex: 1; /* Take remaining space */
  display: flex;
  flex-direction: column;
  min-height: 0; /* Important for flex overflow */
}

.blog-meta {
  flex-shrink: 0; /* Don't shrink */
  margin-bottom: 0.75rem;
}

.blog-category {
  font-size: 1.1rem;
  font-weight: 600;
  color: #64ffda;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Excerpt with fixed height */
.blog-excerpt {
  font-size: 0.9rem;
  color: #a8b2d1;
  line-height: 1.6;
  flex: 1; /* Take available space */
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Limit to 3 lines */
  -webkit-box-orient: vertical;
  margin-bottom: 1rem;
}

/* Modal styling for blog details */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 25, 47, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
  backdrop-filter: blur(8px);
}

.modal-content {
  background: linear-gradient(135deg, #172a45 0%, #0a192f 100%);
  border-radius: 12px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
  position: relative;
  border: 1px solid rgba(100, 255, 218, 0.2);
}

.modal-close {
  position: absolute;
  right: 15px;
  top: 15px;
  background: transparent;
  border: none;
  color: #ccd6f6;
  font-size: 24px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: all 0.2s ease;
  z-index: 1;
}

.modal-close:hover {
  background: rgba(100, 255, 218, 0.1);
  color: #64ffda;
}

.modal-header {
  padding: 20px 30px;
  border-bottom: 1px solid rgba(100, 255, 218, 0.1);
}

.modal-header h2 {
  margin: 0;
  color: #64ffda;
  font-size: 1.8rem;
}

.modal-meta {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.modal-body {
  padding: 30px;
}

.modal-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
}

.modal-details {
  color: #a8b2d1;
}

.modal-text {
  line-height: 1.7;
  margin-bottom: 20px;
}

.modal-text p {
  margin-bottom: 15px;
}

.modal-tags h4 {
  color: #ccd6f6;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.tags-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tag-badge {
  background: rgba(100, 255, 218, 0.1);
  color: #64ffda;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  border: 1px solid rgba(100, 255, 218, 0.2);
}

.modal-footer {
  padding: 20px 30px;
  display: flex;
  justify-content: center;
  border-top: 1px solid rgba(100, 255, 218, 0.1);
}

.modal-btn {
  background: #64ffda;
  color: #0a192f;
  font-weight: 600;
  text-decoration: none;
  padding: 10px 25px;
  border-radius: 4px;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.modal-btn:hover {
  background: transparent;
  color: #64ffda;
  box-shadow: inset 0 0 0 2px #64ffda;
}

/* Carousel customization */
:deep(.carousel__slide) {
  padding: 5px;
}

:deep(.carousel__prev),
:deep(.carousel__next) {
  background-color: rgba(100, 255, 218, 0.2);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  color: #64ffda;
}

:deep(.carousel__prev:hover),
:deep(.carousel__next:hover) {
  background-color: rgba(100, 255, 218, 0.4);
}

/* Animations */
.fade-in {
  animation: fadeIn 1s ease-in-out;
}

.bounce-in {
  animation: bounceIn 1s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }

  50% {
    opacity: 1;
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .blog-card {
    height: 400px;
  }

  .blog-image-container {
    height: 180px;
  }

  .modal-content {
    width: 95%;
  }

  .modal-header h2 {
    font-size: 1.5rem;
  }
}

@media (max-width: 576px) {
  .blog-card {
    height: 380px;
  }

  .blog-image-container {
    height: 120px;
  }

  .blog-excerpt {
    font-size: 0.85rem;
    -webkit-line-clamp: 2;
  }

  .blog-info {
    padding: 1.25rem;
  }

  .modal-image {
    height: 200px;
  }

  .action-btn {
    font-size: 0.8rem;
    padding: 6px 12px;
  }
}
</style>
